.advantages {
  @include reset-list-style;
  padding-block-start: rem(23);
  padding-block-end: rem(10);
  font-size: rem(17);
  font-weight: bold;

  @include tablet {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: rem(5);
    row-gap: rem(30);
  }

  @include desktop {
    column-gap: rem(20);
  }
}
